import React from "react";
import MaterialUiRouterButton from "../Misc/SwitchContextButton";
import { withRouter } from "react-router-dom";
// icons
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";

const SwitchProfile = ({ location }) => {
  const [routeToProfile, setRouteToProfile] = React.useState("client");
  React.useEffect(() => {
    if (location.pathname === "/") setRouteToProfile("client");
    else if (location.pathname.startsWith("/business"))
      setRouteToProfile("business");
  }, [location]);

  return routeToProfile === "client" ? (
    <MaterialUiRouterButton to="/business">
      <span>Soluție saloane</span>
      <ArrowForwardIcon />
    </MaterialUiRouterButton>
  ) : (
    routeToProfile === "business" && (
      <MaterialUiRouterButton to="/home">
        <span>Soluție clienți</span>
        <ArrowForwardIcon />
      </MaterialUiRouterButton>
    )
  );
};

export default withRouter(SwitchProfile);
